/* Fonts */
@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500&display=swap'); //Monsterrat Bold 400/500
@import url('https://fonts.googleapis.com/css2?family=Niconne&display=swap'); //Niconne Regular 400

/* Global UI Variable */
$font-family-text: 'Montserrat', sans-serif;
$font-size-text: 30px;
$font-weight-text: 500;

$font-family-author:  'Niconne', cursive;
$font-size-author: 30px;

$font-weight-generate: 400;

div#root {

  position: relative;

  min-height: 100vh;
  padding: 2em 1em;

  div#quote-box {

    display: flex;
    flex-wrap: wrap;
    align-items: center;

    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);

    max-width: 800px;
    width: 90vw;
    padding: 2em 1em;
    background-color: rgba(178, 178, 248, 0.5);
    border-radius: 0.5em;
    
    div#wrapper {

      flex: 100%;
      display: flex;
      flex-direction: column;
      align-items: flex-end;

      position: relative;

      .fa-quote-left {

        font-size: 60px;

        position: absolute;
        top: -30px;
        left: 50%;
        transform: translate(-50%, -50%);

      }

      p#text {

        font-family: $font-family-text;
        font-size: $font-size-text;
        font-weight: $font-weight-text;
        text-align: center;

        padding: 0.5em;
        width: 100%;
      }

      p#author {

        font-family: $font-family-author;
        font-size: $font-size-author;

        padding: 0.5em;

      }

      .fa-quote-right {

        font-size: 60px;

        position: absolute;
        top: calc(100% + 95px);
        left: 50%;
        transform: translate(-50%, -50%);

      }

    }

    a#tweet-quote {

      .fa-twitter-square {

          color: #1DA1F2;
          font-size: 50px;

          margin: 0.5rem;

      }
    }

    button#new-quote {

      font-size: 20px;
      font-family: $font-family-text;
      font-weight: $font-weight-generate;

      border: none;
      border-radius: 4px;
      outline: none;

      background-color: white;
      height: 44px;
      margin: 0.5rem;
      padding: 0.5rem 2rem;

    }
  }
}