@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Niconne&display=swap);
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

div#root{position:relative;min-height:100vh;padding:2em 1em}div#root div#quote-box{display:flex;flex-wrap:wrap;align-items:center;position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);max-width:800px;width:90vw;padding:2em 1em;background-color:rgba(178,178,248,0.5);border-radius:0.5em}div#root div#quote-box div#wrapper{flex:100% 1;display:flex;flex-direction:column;align-items:flex-end;position:relative}div#root div#quote-box div#wrapper .fa-quote-left{font-size:60px;position:absolute;top:-30px;left:50%;transform:translate(-50%, -50%)}div#root div#quote-box div#wrapper p#text{font-family:"Montserrat",sans-serif;font-size:30px;font-weight:500;text-align:center;padding:0.5em;width:100%}div#root div#quote-box div#wrapper p#author{font-family:"Niconne",cursive;font-size:30px;padding:0.5em}div#root div#quote-box div#wrapper .fa-quote-right{font-size:60px;position:absolute;top:calc(100% + 95px);left:50%;transform:translate(-50%, -50%)}div#root div#quote-box a#tweet-quote .fa-twitter-square{color:#1DA1F2;font-size:50px;margin:0.5rem}div#root div#quote-box button#new-quote{font-size:20px;font-family:"Montserrat",sans-serif;font-weight:400;border:none;border-radius:4px;outline:none;background-color:white;height:44px;margin:0.5rem;padding:0.5rem 2rem}

